import React from "react";
import Sdata from "./Sdata";
import Card from "./Card"





const Services = () => {
    return (
        <>
            <div className="services">
                <h1>Our Services</h1>
                {Sdata.map((val, index) => {
                    return <Card key={index} imgsrc={val.imgsrc} title={val.title} />
                })}

            </div>


        </>


    )


}


export default Services;