import React, { useState } from "react";





const Contact = () => {
    const [data, setData] = useState({
        fname: "", phone: "", email: "", area: "",
    });
    const change = (e) => {
        const { name, value } = e.target
        setData((old) => {
            return {
                ...old,
                [name]: value,
            }
        })

    }
    const submit = (e) => {
        e.preventDefault()
        alert(`Your name is ${data.fname} ,your mobile number is ${data.phone}, your Email is ${data.email} and you have comment ${data.area}`)
    }

    return (
        <>
            <div className="contact">
                <h1>Contact Us</h1>
                <form onSubmit={submit}>
                    <label>Full Name</label>
                    <input type="text" onChange={change} value={data.fname} name="fname" required ></input>
                    <label>Phone</label>
                    <input type="number" onChange={change} value={data.phone} name="phone" required></input>
                    <label>Email Address</label>
                    <input type="email" onChange={change} value={data.email} name="email" required></input>
                    <label>Message</label>
                    <textarea type="text" onChange={change} value={data.area} name="area" required></textarea>
                    <button type="submit">Submit</button>

                </form>
            </div>


        </>


    )


}


export default Contact;