import React from "react"
import { NavLink } from "react-router-dom";




const Navbar = () => {
    function display() {
        document.querySelectorAll(".list").forEach((cur) => {
            if (cur.style.display === "none") {
                cur.style.display = "flex";
            } else {
                cur.style.display = "none";
            }
        })

    }

    return (
        <>

            <ul>
                <NavLink className="left" exact to="/">Vinod Kumar Mehto</NavLink><span onClick={display}>&#9776;</span>
                <NavLink className="list" activeClassName="active" exact to="/services">Services</NavLink>
                <NavLink className="list" activeClassName="active" exact to="/contact">Contact</NavLink>
                <NavLink className="list" activeClassName="active" exact to="/about">About</NavLink>
                <NavLink className="list" activeClassName="active" exact to="/">Home</NavLink>

            </ul>

        </>


    )


}


export default Navbar;