import React from "react";
import { NavLink } from "react-router-dom"


const Common = (props) => {

    return (
        <>
            <div className="content">
                <h1>{props.name}</h1>
                <img style={{ width: 700 }} src={props.img} alt="tech" />
                <p>Hi, Welcome to talented developer world</p>
                <NavLink className="link" exact to={props.visit}>{props.btname}</NavLink>
            </div>

        </>



    )


}



export default Common;


